import React from 'react'

import css from './Dialogs.module.css';
// import DialogsItem from './DialogsItem/DialogsItem';
import Message from './Message/Message';
import userPhoto from '../../assets/images/avatar.png';
import Login from '../Login/Login';
import { Redirect } from 'react-router';
import { Field } from 'redux-form';
import { reduxForm } from 'redux-form';
import {requiredField, maxLengthCreator} from '../../utils/validator/validators';
import {Textarea} from '../Common/FormsControls/FormsControls';

const maxLength50 = maxLengthCreator(50); 
const DialogsForm = (props) => {
    return (
        <form className={css.messagesSend + ' flex aic jcsa'} onSubmit={props.handleSubmit}>
            <Field component={Textarea} validate={[requiredField, maxLength50]} name="newMessageBody" placeholder={"Post message"} className={css.messagesTextarea}  />
            <button className={css.messagesBtn} >Send</button>
        </form>
    )
}
const DialogsReduxForm = reduxForm({form: 'dialogs'})(DialogsForm);


const Dialogs = (props) => {
    // props.isAuth 
    // ? console.log('You are authorizated')
    // : console.log('You are not authorizated')

    let dialogsPage = props.dialogsPage;
    let dialogsElements = dialogsPage.dialogs.map(d => {
        return (
            <div className={css.dialog + ' flex aic'}>

                <div className={css.dialogAvatar}>
                    <img src={
                        d.photos.small === null
                            ? d.photos.small = userPhoto
                            : d.photos.small
                    } alt="dialogsAvatar" />
                </div>
                {d.name}
            </div>
        )
    });
    let messagesElements = dialogsPage.messages.map(m => <Message message={m.message} key={m.id} id={m.id} avatar={m.avatar} />);

    
    
    const onSubmit = (formData) =>{
        props.addMessage(formData.newMessageBody);
    }
    return (
        <div className={css.dialogs}>
            <div className={css.dialogsItem}>
                {dialogsElements}
            </div>
            <div className={css.messages}>
                <div className={css.messagesBox}>
                    {messagesElements}
                </div>
                <DialogsReduxForm onSubmit={onSubmit}/>
            </div>
        </div>
    )

}

export default Dialogs;




