import React from 'react';
import css from './Users.module.css';
import userPhoto from '../../assets/images/avatar.png';
import { NavLink } from 'react-router-dom';
import * as axios from 'axios'
import { usersAPI } from '../../api/api';

let Users = (props) => {
    let totalUsersCount = props.totalUsersCount;
    let pageSize = props.pageSize;
    let pagesCount = Math.ceil(totalUsersCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    return (
        <div>

            <div className={css.pagePaginationBlock}>
                {
                    pages.map(p => {
                        return (
                            <span
                                className={props.currentPage === p 
                                    ? css.selectedPage 
                                    : css.pagePagination
                                } onClick={(e) => { props.onPageChanged(p) }}>{p}</span>
                        )
                    })
                }
            </div>
            {
                props.users.map(u => {
                    return (
                        <div className={css.users} key={u.id}>
                            <div className={css.usersItem + ' flex jcsb aic'}>
                                <NavLink to={'/profile/' + u.id} >
                                    <div className={css.usersInfo + ' flex aic'}>
                                        <div className={css.usersInfoAvatar}>


                                            <span>
                                                <img src={u.photos.small === null
                                                    ? u.photos.small = userPhoto
                                                    : u.photos.small
                                                } alt="avatar" />
                                            </span>


                                        </div>
                                        <div className={css.usersInfoText}>
                                            <h3>{u.name}</h3>
                                            <span>{u.name}</span>
                                        </div>
                                    </div>
                                </NavLink>
                                <div className={css.usersLocation}>
                                    <div className={css.usersCity}>
                                        {"u.location.city"}
                                    </div>
                                    <div className={css.usersCountry}>
                                        {"u.location.country"}
                                    </div>
                                </div>

                                <div className={css.usersFollow}>
                                    {
                                        u.followed
                                            ? <button disabled={props.followingInProgress.some(id=> id === u.id)} onClick={() => {
                                                props.unfollow(u.id);
                                            }}>Unfollow</button>
                                            : <button disabled={props.followingInProgress.some(id=> id === u.id)} onClick={() => {
                                                props.follow(u.id);
                                            }}>follow</button>
                                    }

                                </div>
                            </div>
                        </div>
                    )

                }

                )
            }

        </div>
    )
}

export default Users;