import React from 'react'
import { Field, reduxForm } from 'redux-form';
import css from './MyPosts.module.css';
import Post from './Post/Post';
import {Textarea} from '../../Common/FormsControls/FormsControls';

import {requiredField, maxLengthCreator} from '../../../utils/validator/validators'
const maxLength10 = maxLengthCreator(10);
const MyPostsForm = (props) => {
    return (
        <form className={css.postForm + ' flex aic'} onSubmit={props.handleSubmit}>
            <Field validate={[requiredField, maxLength10] } name={"newPostText"} component={Textarea} className={css.postTextarea} placeholder={"Post message"}/>
            <button className={css.postBtn} >Send</button>
        </form>
    )
}
const MyPostsFormRedux = reduxForm({form: 'MyPosts'})(MyPostsForm);
class MyPosts extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log("RENDER");
        let posts = this.props.posts;
        let PostElements = posts.map(p => <Post key={p.id} name={p.name} message={p.message} likeAmount={p.likeAmount} avatar={p.avatar} />);

        const onSubmit = (formData) =>{

            this.props.addPost(formData.newPostText);
        }
        return (

            <div className={css.postsArea}>
                <h2 className={css.postsTitle}>
                    Добавить пост
                </h2>

                <MyPostsFormRedux onSubmit={onSubmit}/>

                <div className={css.posts}>
                    {PostElements}
                </div>
            </div>

        )
    }
}



export default MyPosts;











