// @ts-check
import React from 'react';
import { connect } from 'react-redux';
import { acceptFollow, acceptUnfollow, requestUsers, 
    setCurrentPage, toggleFollowingInProgress, 
    follow,unfollow } from '../../redux/users-reducer';
import Users from './Users';
import Preloader from '../Common/Preloader/Preloader';
import {usersAPI} from '../../api/api'
import {withAuthRedirect} from '../../hoc/WithAuthRedirect';
import { compose } from 'redux';
import {getUsers, getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount} from '../../redux/users-selectors';

class UsersContainer extends React.Component {

  
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        this.props.requestUsers(this.props.currentPage, this.props.pageSize);
    }
    onPageChanged = (p) => {
        this.props.requestUsers(p, this.props.pageSize);
    }
    render() {
        // console.log("USERS");
        return <>
            {
                this.props.isFetching
                    ? <Preloader />
                    : null


            }
            <Users totalUsersCount={this.props.totalUsersCount} 
            pageSize={this.props.pageSize} users={this.props.users} 
            currentPage={this.props.currentPage} follow={this.props.follow} 
            unfollow={this.props.unfollow} setCurrentPage={this.props.setCurrentPage} 
            onPageChanged={this.onPageChanged} 
             followingInProgress={this.props.followingInProgress} 
             />
        </>
    }
}

let mapStateToProps = (state) => {
    // console.log('mapStateToProps Users'); 
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}



// мы диспатчим не action creator, а его type который он вернёт


export default compose(
    // withAuthRedirect,
    connect(mapStateToProps, 
    {acceptFollow, acceptUnfollow, setCurrentPage, 
    toggleFollowingInProgress, requestUsers, follow, unfollow})
)(UsersContainer);

