

import React from 'react'
import { Redirect } from 'react-router';
import Preloader from '../../Common/Preloader/Preloader';

import css from './ProfileInfo.module.css';

import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {
    if(props.userProfile.length === 0){
        return <Preloader/>
    } 
    let userProfile = props.userProfile;
    
    let userStatus = props.userStatus;
    return(

        <div className={css.ProfileInfo}>
            <div className={css.contentHeader}>
                <img alt="content-header" src={
                    userProfile.photos.large === null
                    ? "https://i.natgeofe.com/n/c9107b46-78b1-4394-988d-53927646c72b/1095_2x1.jpg"
                    : userProfile.photos.large
                    } />
            </div>
            <div className={css.userContent + ' flex aic'}>
                <div className={css.avatar}>
                    <img alt="avatar" src={
                        userProfile.photos.small === null 
                        ? "https://cdn-icons-png.flaticon.com/512/149/149071.png"
                        : userProfile.photos.small
                    } />
                </div>
                <div className={css.info + ' flex aic'}>
                    <div className={css.infoContent + ' flex fxdc' }>
                        <h3>{userProfile.fullName}</h3>
                        <p>
                            <ProfileStatusWithHooks updateStatusThunkCreator={props.updateStatusThunkCreator} editMode={props.editMode} userStatus={userStatus}/>
                        </p>
                    </div>
                    <div className={css.social}>
                        {
                            userProfile.contacts.facebook === null 
                            ? userProfile.contacts.facebook = ""
                            : ""
                        }
                        <a href={'https://' + userProfile.contacts.facebook}><span></span> Facebook</a>
                        <a href={'https://' + userProfile.contacts.github}><span></span> Github</a>
                        <a href={'https://' + userProfile.contacts.vk}><span></span> Vk</a>
                        <a href={userProfile.contacts.twitter}><span></span> Twitter</a>
                        <a href={'https://' + userProfile.contacts.instagram}><span></span> Instagram</a>
                    </div>
                </div>
            </div>
        </div>

    )
}
export default ProfileInfo;

