import React from 'react'

import css from './Post.module.css';
const Post = (props) => {
    return (

        <div className={css.item + ' flex aic'}>
            
            <div className={css.like + ' flex'}> 
                <span className={css.likeAmount}>{props.likeAmount}</span>
                <span onClick={props.increaseLikesAmount}>❤</span>
            </div>  
            <div className={css.itemAvatar}>
                <img src={props.avatar} alt="avatar" />
            </div>
            <div className={css.itemContent}>
                <h3 className={css.userTitle}>{props.name}</h3>
                <p>{props.message}</p>
            </div>
        </div>
    )
}

export default Post;