//@ts-check

import React from "react";
import { useEffect } from "react";
import { useState } from "react";


const ProfileStatusWithHooks = (props) => {

    let [editMode, setEditMode] = useState(false); // стартовое значение false
    let [userStatus, setUserStatus] = useState(props.userStatus);  // два разных хука

    useEffect(() => {
        setUserStatus(props.userStatus);
    }, [props.userStatus] );
    
    /* Hook - функция, которая хранит два параметра:
        1) какая то часть state
        2) функцию, которая меняет эту самую часть state
    */

    const activateEditMode = () => {
        setEditMode(true);
    }
    const deactivateEditMode = () => {
        setEditMode(false);
        props.updateStatusThunkCreator(userStatus); // берём значение из hook
    }

    const onStatusChange = (e) => {
        setUserStatus(e.currentTarget.value)        
    }
    return(
        <>
        <div>
            {
                !editMode 
                && <div onDoubleClick={()=>{
                    activateEditMode();
                }}><span>{props.userStatus}</span></div>

            }
            {
                editMode 
                && <div onBlur={()=>{
                    deactivateEditMode(); 
                }}><input onChange={onStatusChange} value={userStatus} autoFocus={true}/></div>
            }
            
        </div>
        </>
    )
}
export default ProfileStatusWithHooks;

//destructing assignment