//selector - это функция, которая принимает state
//@ts-check








import {createSelector} from "reselect";



//12 минута видео









export const getPageSize = (state) => { 
    return state.usersPage.pageSize;
}

export const getTotalUsersCount = (state) => { 
    return state.usersPage.totalUsersCount;
}

export const getCurrentPage = (state) => { 
    return state.usersPage.currentPage;
}

export const getIsFetching = (state) => { 
    return state.usersPage.isFetching;
}

export const getFollowingInProgress = (state) => { 
    return state.usersPage.followingInProgress;
}






const getUsersSelector = (state) => {  // чтобы по 100 раз не прописывать полный путь.
    return state.usersPage.users;
}


export const getUsers = createSelector(getUsersSelector, (users) => {
    return users.filter(u => true);
}); 



/* 
    getUsers() - это примитимный селектор, который не требует частого обновления

    getUsersSuperSelector() - более сложный селектор, который требует постоянного обновления. 

*/


/*

Hook - это функция


Отличия Функц-й компоненты от Классовой

1. Классовая : 
1.1 Local State
1.2 Life cycle method 

*/