import React from 'react';


const TestApi = (props) => {
    let testApiElements = props.testApi.map((t)=>{

        return(
            <div>
                <li>{t.name}</li>
            </div>
        )
    });
    return(
        <div>
            <h1>test api</h1>
            <ul>
                {testApiElements}
            </ul>
        </div>
    )
}
export default TestApi;