import React from 'react'
import { Route } from 'react-router';
import './App.css';
import DialogsContainer from './components/Dialogs/DialogsContainer';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import Nav from './components/Nav/Nav';
import ProfileContainer from './components/Profile/ProfileContainer';
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import LoginPage from './components/Login/Login';
import { connect } from 'react-redux';
import { getAuthUserData } from './redux/auth-reducer';
import { withRouter } from 'react-router-dom/cjs/react-router-dom.min';
import { compose } from 'redux';
import { initialize } from 'redux-form';
import { initializeApp } from './redux/app-reducer';
import Preloader from './components/Common/Preloader/Preloader';



class App extends React.Component {
  
  componentDidMount(){
    this.props.initializeApp();
  
  }
  
  render() {


    if(!this.props.initialized) {
      return <Preloader/>
    }
    return (

      <div className="app-wrapper">
        <HeaderContainer />
        {/* <Nav state={props.state.navBlock}/> */}
        {/* {this.props.isAuth && <Nav /> /*удаление nav у неавторизованных*/  }  

        <Nav /> 
        
        <div className="app-wrapper-content">

          <Route path='/profile/:userId?' render={e => <ProfileContainer />} />

          <Route exact path='/messages' render={e => <DialogsContainer />} />

          <Route exact path='/users' render={e => <UsersContainer />} />




          <Route path='/news' render={e => <News />} />
          <Route path='/music' render={e => <Music />} />
          <Route path='/settings' render={e => <Settings />} />

          <Route path='/login' render={e => <LoginPage />} />

        </div>
      </div>



    )
  }
}

const mapStateToProps = (state) => {
  return {
    initialized: state.app.initialized,
    isAuth: state.auth.isAuth
  }
}

export default compose(
  withRouter,
  connect(mapStateToProps, {initializeApp})
)(App)
