import React from 'react';
import Dialogs from './Dialogs';
import { addMessage, getUsers } from '../../redux/dialogs-reducer';
import { connect } from 'react-redux';
import { dialogsAPI } from '../../api/api';
import { Redirect } from 'react-router';
import {withAuthRedirect} from '../../hoc/WithAuthRedirect';
import { compose } from 'redux';


class DialogsContainer extends React.Component {
    constructor(props) {
        super(props);
    }
    componentDidMount(){
        this.props.getUsers(this.props.currentDialogsPage, this.props.totalDialogsCount);   
    }
    
    render() {
        
        return(<Dialogs dialogsPage={this.props.dialogsPage} {...this.props}/>)
    }
}

let mapStateToProps = (state) => {
    return {
        dialogsPage: state.dialogsPage,
        totalDialogsCount: state.dialogsPage.totalDialogsCount,
        currentDialogsPage: state.dialogsPage.currentDialogsPage,
    }
}

export default compose(
    connect(mapStateToProps, {addMessage, getUsers}),
    withAuthRedirect
)(DialogsContainer);