import {usersAPI} from '../api/api';

let initialState = {
    users: [],
    pageSize: 100,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
    fake: 10

};
const usersReducer = (state = initialState, action) =>{
    switch (action.type) {   
        case "FAKE": {
            return {
                ...state, 
                fake: state.fake + 1
            }
        }
        
        case FOLLOW: 
            return { 
                ...state, 
                users: state.users.map(u => {
                    if(u.id === action.userId){                        
                        return {...u, followed: true}; // если id совпадает возвращаем копию
                    }
                    return u; // если не совпадает, возвращает то же самое
                })
            }    
        case UNFOLLOW: 
        
            return {
                ...state,
                users: state.users.map(u => {
                    if(u.id === action.userId){
                        
                        return {...u, followed: false};
                    }
                    return u;
                })
            }
            
        case SET_USERS: {
            return {...state, users: action.users} // Получаем users из dispatch и перезатираем старый state
        }
        case SET_CURRENT_PAGE: {
            return { ...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return { ...state, totalUsersCount: action.totalUsersCount}
        }
        case TOGGLE_IS_FETCHING: {
            return { ...state, isFetching: action.isFetching}
        }
        case TOGGLE_FOLLOWING_IN_PROGRESS: {
            return{ 
                ...state, 
                followingInProgress: action.inProgress // Если true  то - (true бывает если нажали на кнопку) 
                ? [...state.followingInProgress, action.userId] // пушим id пользователя чью кнопку отключить. Но это на пару сек, потом срабатывает то что снизу
                : state.followingInProgress.filter(id => id != action.userId) //  Если false - (false бывает если запрос уже пришёл в then)   
            }
        }
        default:
            return state;
    }
}

const 
    // SHOW_MORE = 'SHOW-MORE',
    FOLLOW = 'FOLLOW',
    UNFOLLOW = 'UNFOLLOW',
    SET_USERS = 'SET-USERS',
    SET_CURRENT_PAGE = 'SET-CURRENT-PAGE',
    SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT',
    TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING',
    TOGGLE_FOLLOWING_IN_PROGRESS = 'TOGGLE_FOLLOWING_IN_PROGRESS';

// let showMoreActionCreator = () => ({type: SHOW_MORE });
export let acceptFollow = userId => ({type: FOLLOW, userId});
export let acceptUnfollow = userId => ({type: UNFOLLOW, userId});
export let setUsers = users => ({type: SET_USERS, users});
export let setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});
export let setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, totalUsersCount});
export let toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
export let toggleFollowingInProgress = (inProgress, userId) => ({type: TOGGLE_FOLLOWING_IN_PROGRESS, inProgress, userId});


//thunks

export const requestUsers = (page,pageSize) => {
    return(
        (dispatch) => {

            dispatch(toggleIsFetching(true));
            dispatch(setCurrentPage(page));
            usersAPI.getUsers(page, pageSize).then((data) => 
            {
                dispatch(setUsers(data.items));
                dispatch(setTotalUsersCount(data.totalCount));
                dispatch(toggleIsFetching(false));
                
            });
        }
    )
    
} 
export const follow = (userId) => {
    return(
        (dispatch) => {

            dispatch(toggleFollowingInProgress(true, userId));
            usersAPI.follow(userId).then((response) => {
                if(response.data.resultCode == 0) {
                    dispatch(acceptFollow(userId))
                }
                dispatch(toggleFollowingInProgress(false, userId));
            });
        }
    )
    
} 
export const unfollow = (userId) => {
    return(
        (dispatch) => {

            dispatch(toggleFollowingInProgress(true, userId));
            usersAPI.unfollow(userId).then((response) => {
                if(response.data.resultCode == 0) {
                    dispatch(acceptUnfollow(userId))
                }
                dispatch(toggleFollowingInProgress(false, userId));
            });
        }
    )
    
} 


export default usersReducer;


