import { applyMiddleware, combineReducers, createStore } from "redux";
import profileReducer from "./profile-reducer";
import dialogsReducer from "./dialogs-reducer";
import navReducer from "./nav-reducer";
import usersReducer from "./users-reducer";
import authReducer from "./auth-reducer";
import thunkMiddleWare from 'redux-thunk';
import { reducer as formReducer } from 'redux-form'; 
import appReducer from "./app-reducer";



let reducers = combineReducers({
    profilePage: profileReducer,
    dialogsPage: dialogsReducer,
    navBlock: navReducer,
    usersPage: usersReducer,
    auth: authReducer,
    app: appReducer,
    form: formReducer
});


//redux
let store = createStore(reducers, applyMiddleware(thunkMiddleWare)); 


export default store;
window.store = store;


// Детерминирование - получение того же результата
// Идемпотентность - на выходе мы должны получить то же самое, что и ввели. Принцип чистой функции 
// Компонента на входе получает props, на выходе выводит JSX
//createStore - Создание общего store
// combineRedusers - объединение редьюсеров


//Reducer - это чистая функция которая на входе принимает старый state