import React from 'react';
import { NavLink } from 'react-router-dom';
//  import FriendItem from './FriendItem/FriendItem';

import css from './Nav.module.css';
const Nav = (props) => {
    // let FriendsElements = props.state.friends.map(f => <FriendItem friendAvatar={f.friendAvatar} name={f.name}/>);
    return (
        <nav className={css.nav}>
            
            <div className={css.item}>
                
                <NavLink to="/profile" className="flex aic" activeClassName={css.active}>
                    <span><img src="https://cdn-icons-png.flaticon.com/512/1077/1077114.png" alt="Profile Icon" /></span>
                    Profile
                </NavLink>
            </div>
            <div className={css.item}>
                <NavLink to="/messages" className="flex aic" activeClassName={css.active}>
                    <span><img src="https://cdn-icons-png.flaticon.com/512/134/134718.png" alt="message" /></span>
                    Messages
                </NavLink>
            </div>
            <div className={css.item}>
                <NavLink to="/news" className="flex aic" activeClassName={css.active}>
                    <span><img src="https://cdn-icons-png.flaticon.com/512/483/483675.png" alt="news" /></span>
                    Videos
                </NavLink>
            </div>
            <div className={css.item}>
                <NavLink to="/music" className="flex aic" activeClassName={css.active}>
                <span><img src="https://cdn-icons-png.flaticon.com/512/134/134718.png" alt="message" /></span>  
                    Music
                </NavLink>
            </div>
            <div className={css.item}>
                <NavLink to="/settings" className="flex aic" activeClassName={css.active}>
                    <span><img src="https://cdn-icons-png.flaticon.com/512/503/503849.png" alt="setting" /></span>
                    Settings
                </NavLink>
            </div>
            <div className={css.item}>
                <NavLink to="/users" className="flex aic" activeClassName={css.active}>
                    <span><img src="https://cdn-icons-png.flaticon.com/512/503/503849.png" alt="users" /></span>
                    Users
                </NavLink>
            </div>
            <div className={css.friends + ' flex jcsb'}>
                {/* {FriendsElements} */}
            </div>
        </nav>
    )
}
export default Nav;