import {profileAPI,usersAPI} from '../api/api'
//Стартовое значение state profilePage
let initialState = {
    posts: [
        { id: 1, name: "Номто Балтуков", message: "Привет", likeAmount: 30, avatar: 'https://i.ibb.co/n1vt8JK/avatar.png' },
        { id: 1, name: "Номто Балтуков", message: "Привет)", likeAmount: 0, avatar: "https://i.ibb.co/n1vt8JK/avatar.png" },

    ],
    newPostText: 'Пост', //значение которое мы меняем на ходу в BLL
    userProfile: [],
    userStatus: null,
    editMode: false,
    testApi: []
}


// Функция которая узнает, какая кнопка на пульте нажата
const profileReducer = (state = initialState, action) => {
    let type = action.type;
    switch (type) {
        case ADD_POST:
            let newPost = {
                id: 3,
                message: action.newPostText, // Добавляем в BLL, чтобы изменить UI
                name: 'Nastya',
                likeAmount: 0,
                avatar: 'https://i.ibb.co/n1vt8JK/avatar.png'
            }
            return {
                ...state,
                posts: [...state.posts, newPost]
            };
            

            

        case UPDATE_NEW_POST_TEXT:
            return {
                ...state,
                newPostText: action.newText
            };
        case SET_USER_PROFILE: {
            return{
                ...state,
                userProfile: action.userProfile
            }
        }
        case SET_USER_STATUS: {
            /*alert('is working');*/
            return{
                ...state,
                userStatus: action.userStatus
            }
        }
        case TOGGLE_EDIT_MODE: {
            return {
                ...state,
                editMode: action.isEdit
            }
        }
        case SET_TEST_API: {
            return {
                ...state,
                testApi: action.test
            }
        }
        default:
            return state;
    }

}


// Action



//Кнопочки на пульте (action.type)
const
    ADD_POST = 'ADD-POST',
    UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT',
    SET_USER_PROFILE = 'SET_USER_PROFILE',
    SET_USER_STATUS = 'SET_USER_STATUS',
    TOGGLE_EDIT_MODE = 'TOGGLE_EDIT_MODE',
    SET_TEST_API = 'SET_TEST_API';

// Кнопка для отправки поста
export const addPostActionCreator = (newPostText) => ({ type: ADD_POST, newPostText });
// Кнопка для обновления текста в режиме онлайн
export const updateNewPostTextActionCreator = text => ({ type: UPDATE_NEW_POST_TEXT, newText: text });
export const setUserProfile = userProfile => ({ type: SET_USER_PROFILE, userProfile });
export const setUserStatus = userStatus => ({ type: SET_USER_STATUS, userStatus });
export const toggleEditMode = isEdit => ({ type: TOGGLE_EDIT_MODE, isEdit });
export const setTestApi = test => ({ type: SET_TEST_API, test });
//Thunk 
export const getUsersThunkCreator = (id) => {
    return(
        (dispatch) => {
            profileAPI.getProfile(id).then((data) => {
                dispatch(setUserProfile(data));
            });
        }
    )
}
export const getStatusThunkCreator = (id) => {
    return( (dispatch) => {
            profileAPI.getStatus(id).then((data) => {
                if(data === null){data = "Нет статуса";}
                dispatch(setUserStatus(data)); // диспатчим статус в bll
            });
        }
    )
}
export const updateStatusThunkCreator = (status) => {
    return( (dispatch) => {
            profileAPI.updateStatus(status).then((data) => {
                // alert('d');
                // debugger;
                if(data.data.resultCode == 0){
                    dispatch(setUserStatus(status));
                }
            });
        }
    )
}
export const setTestApiThunk = () => {
    return (dispatch) => {
        usersAPI.getTestApi().then(data=>{
            return(
                dispatch(setTestApi(data))
            )
        })
    }
}
export default profileReducer;
