import React from 'react'
import MyPostsContainer from './MyPosts/MyPostsContainer';

import css from './Profile.module.css';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import TestApiContainer from './TestApi/TestApiContainer';



const Profile = (props) => {
    return (
        <div className={css.profile}>
            <ProfileInfo updateStatusThunkCreator={props.updateStatusThunkCreator} toggleEditMode={props.toggleEditMode} editMode={props.editMode} userProfile={props.userProfile} userStatus={props.userStatus} isAuth={props.isAuth} myId={props.myId} /> 
            <MyPostsContainer />
            <TestApiContainer setTestApiThunk={props.setTestApiThunk} testApi={props.testApi}/>
        </div> 
    )   
}

export default Profile;
