import * as axios from 'axios';

const instanse = axios.create({
    
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "eee580e9-41e0-4287-85f4-dd8d66773124"
    }

});
const instanseNew = axios.create({
    
    baseURL: 'https://my-json-server.typicode.com/dithmaa/demoapi/'

});


export const usersAPI = {
    getUsers (currentPage = 1, pageSize = 10) {
        return (
            instanse.get(`users?page=${currentPage}&count=${pageSize}`)
            .then((response) => {
                return(
                    response.data
                );
            })
        )
    },
    unfollow(userId) {
        return(
            instanse.delete(`follow/${userId}`)
        )
    },
    follow(userId) {
        return(
            instanse.post(`follow/${userId}`)
        )
    },
    
    getTestApi(){
        return(
            instanseNew.get(`people`).then(response=>{
                return(
                    response.data
                )
            })
        )
        
    }
}
export const profileAPI = {
    getProfile (id) {
        return (
            instanse.get(`profile/` + id)
            .then((response) => {
                return(
                    response.data
                );
            })
        )
    },
    getStatus (id = 1) {
        return (
            instanse.get(`profile/status/` + id)
            .then((response) => {
                return(
                    response.data
                );
            })
        )
    },
    updateStatus(status){
        return instanse.put(`profile/status`, {status: status})
    }
}

export const authAPI = {
    authMe () {
        return (
            instanse.get(`auth/me`)
            .then((response) => {
                return(
                    response.data
                );
            })
        )
    }, 
    login (email,password,rememberMe = false){
        return (
            instanse.post(`auth/login`, { email,password,rememberMe }).then((response) => {
                return(
                    response.data
                );
            })
        )
    },
    logout (){
        return (
            instanse.delete(`auth/login`).then((response) => {
                return(
                    response.data
                );
            })
        )
    },

}
export const dialogsAPI = {
    getUsers (currentDialogsPage = 1, totalDialogsCount = 10) {
        return (
            instanse.get(`users?page=${currentDialogsPage}&count=${totalDialogsCount}`)
            .then((response) => {
                return(
                    response.data
                );
            })
        )
    }
}