import React from 'react';
import { connect } from 'react-redux';
import { reduxForm, Field } from 'redux-form'; 
import { maxLengthCreator, requiredField } from '../../utils/validator/validators';
import { Input } from '../Common/FormsControls/FormsControls';
import { login } from '../../redux/auth-reducer';

import css from './Login.module.css';
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';

const maxLength25 = maxLengthCreator(25);
const LoginForm = (props) => {
    return (
        <form className={css.form + ' flex fxdc'} onSubmit={props.handleSubmit}>
            <h2 className={css.formTitle}>
                Sign in
            </h2>
            <div className={css.formInputs}>
                <div className={css.input}>
                    <h3 className={css.inputTitle}>Email: </h3>
                    <Field type="text" validate={[requiredField, maxLength25]}  name={"email"} placeholder={"Email"} component={Input} />
                </div>
                <div className={css.input}>
                    <h3 className={css.inputTitle}>Password: </h3>
                    <Field type="password" validate={[requiredField, maxLength25]}  name={"password"} placeholder={"Password"} component={Input}  />
                </div>
            </div>
            {
                props.error && <div className={css.formGeneralError}>
                    {props.error}
                </div>
            }
            <div className={css.formButtons + ' flex jcsb aic'}>
                <label className={css.formCheckbox}>
                    <Field type={"checkbox"} name={"rememberMe"} component={"input"} />
                    <span>Remember me</span>
                </label>
                <input type="submit" className={css.formSignIn} value={"Отправить"}>

                </input>
            </div>
            <a href="#" className={css.signupLink}>
                Sign up
            </a>
        </form>
    )
}
const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);
const mapStateToProps = (state) => {
    return { 
        isAuth: state.auth.isAuth
    }
}
const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    } 

    if(props.isAuth){
        return <Redirect to={"/profile"} />
    }

    return (
        <div className={css.login}>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    )
}

export default connect(mapStateToProps, {login})(Login);