import { dialogsAPI } from "../api/api";
const
    ADD_MESSAGE = 'ADD-MESSAGE',
    SET_USERS = 'SET-USERS';
let initialState = {
    messages: [
        { id: 1, message: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Earum laboriosam labore possimus aperiam! Vel eveniet dolor ullam distinctio minima magni similique, hic ratione animi nisi consequatur perferendis aspernatur necessitatibus et!", avatar: "https://i.ibb.co/n1vt8JK/avatar.png" },
        

    ],
    dialogs: [],
    totalDialogsCount: 5,
    currentDialogsPage: 1
}
const dialogsReducer = (state = initialState, action) => {
    let type = action.type;
    switch (type) {
        case ADD_MESSAGE:
            let newMessage = {
                id: 4,
                message: action.newMessageBody, //Добавляем в BLL, чтобы изменился UI
                avatar: "https://i.ibb.co/n1vt8JK/avatar.png"
            }

            return {
                ...state,
                messages: [...state.messages, newMessage]
            };
            
        
            
        case SET_USERS: {
            return {...state, dialogs: action.users} // Получаем users из dispatch и перезатираем старый state
        }
        default:
            return state;

    }
}
export const addMessage = (newMessageBody) => ({ type: ADD_MESSAGE, newMessageBody });

export let setUsers = users => ({type: SET_USERS, users});

//Thunk 
export const getUsers = (currentDialogsPage, totalDialogsCount) => {
    return(
        (dispatch) => {
            dialogsAPI.getUsers(currentDialogsPage, totalDialogsCount).then((data) =>{
                dispatch(setUsers(data.items));
            });  
        }
     )
}
export default dialogsReducer;



