import React from 'react'

import css from './Message.module.css';
const Message = (props) => {
    return (
        <div className={css.message + ' flex aic'}>
            <div className={css.messageAvatar}>
                <img src={props.avatar} alt="messageAvatar" />
            </div>
            {props.message}
        </div>
    )
}



export default Message;