import React from 'react'
import Profile from './Profile';
import * as axios from 'axios';
import { connect } from 'react-redux';
import { getUsersThunkCreator, getStatusThunkCreator, updateStatusThunkCreator,setTestApiThunk} from '../../redux/profile-reducer';
import { Redirect, withRouter } from 'react-router';
import { profileAPI } from '../../api/api'
import { withAuthRedirect } from '../../hoc/WithAuthRedirect';
import { compose } from 'redux';
class ProfileContainer extends React.Component{
    constructor(props){
        super(props);
    }
    componentDidMount(){
        
        let userId = this.props.match.params.userId; // из url, благодаря withRouter
        if(!userId) {
            userId = this.props.authorizedUserId; // если в url нет айди, покажи мой айди
            if(!userId){
                this.props.history.push("/login");
            }
        }

        this.props.getUsersThunkCreator(userId);


        //статус
        this.props.getStatusThunkCreator(userId);


  
        
    }
    render(){
        // console.log("Render Profile");
        
        return   (
        <Profile {...this.props} userProfile={this.props.userProfile} updateStatusThunkCreator={this.props.updateStatusThunkCreator} userStatus={this.props.userStatus} setTestApi={this.props.setTestApi}/>
        )
    }
    
}

let AuthRedirectComponent = withAuthRedirect(ProfileContainer);

const mapStateToProps = (state) => {
    // console.log('mapStateToProps Profile'); 
    return({
        userProfile: state.profilePage.userProfile,
        userStatus: state.profilePage.userStatus,
        authorizedUserId: state.auth.id,
        isAuth: state.auth.isAuth,
        editMode: state.profilePage.editMode,
        testApi: state.profilePage.testApi
    })
}


export default compose(
    connect(mapStateToProps, {getUsersThunkCreator, getStatusThunkCreator, updateStatusThunkCreator,
        setTestApiThunk }),
    withRouter,
    // withAuthRedirect
)(ProfileContainer)

